.note-view{
	width: 100%;
	height: 100%;
	overflow: auto;
	padding: 32px;
}

.note {
	margin: 12px 0;
	animation: anim 0.35s linear;
	transform-origin: bottom;
	transition: opacity 0.5s;
}
.note-text {
	height: 23px;
}
@media (min-width: 576px) {
	.note {
		display: inline-block;
		width: 40%;
		margin-left: 32px;
	}
}

@media (min-width: 874px) {
	.note {
		width: 30%;
		display: inline-block;
		margin-left: 22px;
	}
}

@media (min-width: 1200px) {
	.note {
		width: 15%;
		display: inline-block;
		margin-left: 26px;
	}
}

@keyframes anim {
	from {
		transform: scale(0);
	}
}
.fond-alert {
	transition: opacity 1s;
	transform: scale(0);
	opacity: 0;
	position: absolute;
	z-index: 9;
	background-image: linear-gradient(180deg,rgba(0, 0, 0, 0.74),rgba(0, 0, 0, 0.808));
	top: 0;left: 0;right: 0;bottom: 0;
	width: 100%;
	height: 100%;	
}

.alert {
	opacity: 1;
	transform: scale(0);
	transform-origin: bottom;
	transition: transform 0.4s;
	background-color: white;
	padding: 0;
	font-size: 19px;
	border: 0px;
	border-radius: 8px;
}
.alert-dim {
	z-index: 10;
	position: absolute;
	top: 0;left: 0;right: 0;bottom: 0;
	width: 336px;
	height: 50%;
	margin-top: 25%;
	margin-bottom: 25%;
	margin-right: calc((100% - 336px)/2);
	margin-left: calc((100% - 336px)/2);
}
@media (min-width: 576px) {
	.alert-dim {
		width: 440px;
		margin-right: calc((100% - 440px)/2);
		margin-left: calc((100% - 440px)/2);
	}
}

@media (min-width: 874px) {
	.alert-dim {
		width: 50%;
		margin: 25%;
	}
}

@media (min-width: 1200px) {
	.alert-dim {
		width: 30%;
		margin: 35%;
		margin-top: 12.5%;
		margin-bottom: 12.5%;
	}
}

.close {
	position: absolute;
	top: 5px;
	right: 10px;
	font-size: 40px;
}

.alert input , .alert .tf {
	border: 1px solid transparent;
	background-color: rgb(10, 184, 160);
	color: white;
	border-radius: 8px 8px 0 0;
	padding: 32px;
	width: 100%;
	height: 95px;
	margin: 0;
}

.alert textarea,.alert .df{
	border: 1px solid transparent;
	background-color: whitesmoke;
	width: 100%;
	height: calc(100% - 95px);
	margin: 0;
	padding: 12px 32px;
	overflow: auto;
	border-radius: 0 0 8px 8px;
	overflow-wrap: break-word;
}

.alert .modify {
	width: 100%;
	justify-content: flex-start;
	align-items: center;
}
.alert .modify button {
	width: 80%;
	height: 45px;
	margin-top: 10px;
	margin-left: 10%;
	font-weight: 500;
	font-size: 1.1rem;
}

.window-mode .alert, .window-mode .fond-alert{
	transform: scale(1);
	opacity: 1;
}

.shock {
	animation: shock 1s linear;
}

@keyframes shock {
	0% {
		transform: translateX(-4px);
	} 25% {
		transform: translateX(5px);
	} 50% {
		transform: translateX(-6px);
	} 75% {
		transform: translateX(4px);
	} 100% {
		transform: translateX(-1px);
	}
}

.btn-circle {
	border-radius: 100px;
	width: 45px;
	height: 45px;
	box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.486);
}

.btn-absolute {
	position: absolute;
	z-index: 5;
	bottom: -22px;
	right: -22px;
}

#ntitle {
	font-weight: 600;
	font-size: 32px;
}