.search-bar .gg-search {
	--ggs: 1.2
}

.search-bar {
	position: absolute;
	top: 0;
	transition: transform 0.6s;
	width: 94%;
	margin-left: 3%;
	margin-top: 12px;
	background-color: white;
	padding: 4px;
	border-radius: 8px;
}

@media (min-width: 576px) {
	.search-bar {
		width: 80%;
		margin-left: calc(5% + var(--size-icon));
	}
}

@media (min-width: 874px) {
	.search-bar {
		width: 70%;
		margin-left: calc(var(--size-icon) + 10%);
	}
}

@media (min-width: 1200px) {
	.search-bar {
		width: 50%;
		margin-left: calc(var(--size-icon) + 20%);
	}
}

#search {
	border: 1px solid transparent;
	outline-style: none;
	font-size: 19px;
	font-weight: 500;
	width: calc(100% - 48px);
	padding-left: 22px;
}

#search:focus {
	border: 1px solid transparent;
	box-shadow: none;
}

.btn-search {
	border: 1px solid transparent;
	background-color: transparent;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 48px !important;
	height: 42px !important;
}

.btn-search:hover {
	background-color: rgba(245, 245, 245, 0.568);
}