.menu {
	padding: 0;
	width: 100%;
	height: var(--size-icon);
	margin: 0;
}
.content {
	width: 100%;
	height: calc(100% - var(--size-icon));
	margin: 0;
	padding: 0;
}
.blue {
	background-image: var(--bg-lg-andie)
}
@media (min-width: 576px) {
	.menu {
		width: var(--size-icon);
		height: 100%;
	}
	.content {
		width: calc(100% - var(--size-icon));
		height: 100%;
	}
}
