.df table {
	border-collapse: collapse;
}
.df th,.df td {
	text-align: center;
	border: 1px solid rgba(0, 0, 0, 0.582);
	padding: 8px 12px;
}
.df th {
	background-color: #0f0f0fcb;
	color: #eee;
}
.df fieldset {
	border: 1px solid silver;
	padding: 12px;
	border-radius: 5px;
}
.df legend {
	padding: 12px;
	width: fit-content;
}
.df .instruction {
	border: 1px solid silver;
	padding: 12px;
	border-radius: 8px;
}
.df .instruction span {
	border-radius: 6px;
	color: white;
}
.df .red {
	background-color: #d42d2d;
}
.df .green {
	background-color: cadetblue;
}
.df .blue {
	background-color: #0098ff;
}

.df .struct {
	padding: 14px;
	border: 1px solid silver;
	border-radius: 8px;
	margin-top: 12px;
}
.df h3,.df h5 {
	margin-top: 12px;
}
.df .arboressence {
	width: 25px;
	height: 25px;
	display: inline-block;
	border-left: 1px solid silver;
	border-bottom: 1px solid silver;
}
.df .m-25 {
	margin-left: 25px;
}