.btn-i .gg-add-r,.btn-i .gg-search,.btn-i .gg-trash {
	color: white;
	--ggs: 1.5;
}
@media (min-width: 874px) {
	.btn-i .gg-add-r,.btn-i .gg-search,.btn-i .gg-trash {
		--ggs: 1.7;
	}
}
.i-btn {
	padding: 0;
}
.btn-i {
	width: var(--size-icon);
	height: var(--size-icon);
}
.btn-i button {
	border: 1px solid transparent;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}