
.show-mode .fond , .show-mode .popup-content{
	transform: translateY(0);
	opacity: 1;
}
.fond {
	transition: opacity 1s;
	transform: translateY(-120%);
	opacity: 0;
	position: absolute;
	top: 0;
	background-image: linear-gradient(180deg,rgba(0, 0, 0, 0.74),rgba(0, 0, 0, 0.808));
	width: 100%;
	height: 100%;
	z-index: 7;
}

.popup-content {
	opacity: 1;
	transform: translateY(-120%);
	transform-origin: bottom;
	transition: transform 0.4s;
	z-index: 10;
	position: absolute;
	top: 0;left: 0;
	width: 94%;
	height: 160px;
	margin-left: 3%;
	margin-top: 12px;
	background-color: white;
	border-bottom: 1px solid red;
	border-radius: 8px;
}

.popup-description,.popup-action {
	width: 100%;
	border-radius: 8px;
}

.popup-description {
	height: 50%;
	padding: 32px;
	background-color: whitesmoke;
}
.popup-action {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-end;
	align-items: center;
	padding: 22px;
	height: 50%;
}
.popup-action button {
	margin-left: 12px;
}
@media (min-width: 576px) {
	.popup-content {
		width: 60%;
		margin-left: calc(20% + var(--size-icon));
	}
}

@media (min-width: 874px) {
	.popup-content {
		width: 50%;
		margin-left: calc(var(--size-icon) + 25%);
	}
}

@media (min-width: 1200px) {
	.popup-content {
		width: 40%;
		margin-left: calc(var(--size-icon) + 30%);
	}
}