:root {
	--w-add-note: 95%;
	--m-add-note: 2.5%
}

@keyframes tada {
	from {
		opacity: 0;
	}
}

@media (min-width: 430px) {
	.add-note {
		--w-add-note: 80%;
		--m-add-note: 10%;
	}
}
@media (min-width: 479px) {
	.add-note {
		--w-add-note: 439.797px;
		--m-add-note: calc((100% - 439.797px)/2);
	}
}
@media (min-width: 570px) {
	.add-note {
		--w-add-note: 439.797px;
		--m-add-note: 100px;
	}
}
@media (min-width: 584px) {
	.add-note {
		--w-add-note: 439.797px;
		--m-add-note: calc(var(--size-icon) + 10%);
	}
}


.add-note {
	width: var(--w-add-note);
	background-color: white;
	padding: 32px 20px;
	border-radius: 8px;
	position: absolute;
	top: 0;
	z-index: 6;
	margin-left: var(--m-add-note);
	margin-top: 20px;
	transition: transform 0.76s;
}
